import React, { memo, forwardRef, isValidElement, ReactNode } from 'react';
import { mergeRestProps, MergePropsType, mergeClassName } from '../../common';
import { IconButtonWrap } from './styled';

type IIconButtonProps = MergePropsType<{
  /**
   * 图标
   */
  icon?: ReactNode;
  /**
   * 禁用态
   */
  disabled?: boolean;
  /**
   * 激活态
   */
  active?: boolean;
}, React.ButtonHTMLAttributes<HTMLButtonElement>>;

export const IconButton = memo(

  forwardRef(

    (props: IIconButtonProps, ref: React.Ref<HTMLButtonElement>) => {

      const {
        icon = <svg />,
        disabled,
        active,
        ...rest
      } = props;

      const size = isValidElement(icon) && icon.props && (icon.props as any).size;
      const is24 = size === 'large' || size >= 24;

      return <IconButtonWrap
        ref={ref}
        is24={is24}
        disabled={disabled}
        {...mergeRestProps(rest, mergeClassName('wdn-icon-but', active ? 'active' : null))}
      >
        {icon}
      </IconButtonWrap>;
    }
  )
)

IconButton.displayName = 'IconButton';